.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.page__body {
  box-sizing: border-box;
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 4px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__info {
  margin-bottom: 16px;
}

.card__info-img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__info-text {
  margin: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info-code {
  color: #616070;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-total {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: block;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.stars__container {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
  margin-right: 0;
}

.stars__rate-text {
  font-size: 10px;
  line-height: 14px;
}

